export default {
	data() {
		return {
			imgList: [],
			maxImg: true,
			title: '',
			ftitle: '',
			price: '',
			classify: 0,
			max: '',
			min: '',
			unit: '',
			tempFilePaths: [],
			shopid: '',
			goodsTypes: '',
			typesList: [],
			typeId: 0,
			type: 0,
			imgUrl: '',
			goodsid:0


		}
	},
	onLoad(option) {
		if (option.type == 0) {

			uni.request({
				url: this.$bashUrl + '/index.php/home/Api/goods_info',
				method: "post",
				data: {
					good_id: Number(option.id)
				},
				success: res => {
					console.log(res)
					this.price = res.data.price
					this.title = res.data.name
					this.ftitle = res.data.content
				}
			})
		}
		this.goodsid = option.id
		this.shopid = option.shopid
		this.imgUrl = this.$imgUrl
		var that = this
		console.log(option)
		this.type = option.type
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/type_lists',
			method: 'post',
			data: {
				store_id: option.shopid
			}
		}).then(res => {
			console.log(res)
			this.typesList = res[1].data
		})

	},
	onShow: function() {

	},
	onHide: function() {
		// 隐藏键盘
		uni.hideKeyboard()
	},
	methods: {
		bindChange: function(e) {
			console.log(e)
			this.classify = e.detail.value

		},
		UpImg: function(e) {
			var that = this
			uni.chooseImage({
				count: 9, // 上传照片张数
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					that.tempFilePaths = res.tempFilePaths;
					// 显示图片
					// 对传递过来的字符串进行改造,符合显示要求
					var s1 = JSON.stringify(res.tempFilePaths).slice(1, -1)
					that.imgList = s1.split(",")
					if (that.imgList.length >= 9) that.maxImg = false;
					for (var i = 0; i < that.imgList.length; i++) {
						that.imgList[i] = that.imgList[i].slice(1, -1)
					}
				},
				fail: function(res) {

				}
			});
		},
		submit: function() {
			//  提交
			var that = this
			// 上传表单\
			if (this.type == 0) {
				console.log(that.tempFilePaths[0])
				uni.uploadFile({
					url: that.$bashUrl + '/index.php/home/Api/save_goods',
					method: "post",
					filePath: that.tempFilePaths[0],
					name: 'file',
					formData: {
						"store_id":this.shopid,
						'goodsid': this.goodsid,
						'name': this.title,
						'content': this.ftitle,
						'price': this.price,
						'type': Number(this.typesList[this.classify].id)
					},
					success: (d) => {
						console.log(d)
					uni.showToast({
						title: '商品更新成功',
						icon:'none',
						duration:2000,
						mask:true,
							success() {
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},2000)
								// 清空数据
								that.title = ''
								that.ftitle = ''
								that.price = ''
								that.classify = ''
								that.max = ''
								that.min = ''
								that.unit = ''
							},
							fail() {
								// 清空数据
								that.title = ''
								that.ftitle = ''
								that.price = ''
								that.classify = ''
								that.max = ''
								that.min = ''
								that.unit = ''
							}
						})
					},
					fail: function(err) {
						console.log('商品更新失败')
					}
				})
			} else {
				console.log(this.typesList[this.classify].id)
				uni.uploadFile({
					url: that.$bashUrl + '/index.php/home/Api/new_goods/',
					filePath: that.tempFilePaths[0],
					name: 'file',
					formData: {
						'store_id': this.shopid,
						'name': this.title,
						'content': this.ftitle,
						'price': this.price,
						'type': Number(this.typesList[this.classify].id)
					},
					success: (uploadFileRes) => {
						uni.showToast({
							title: '商品上传成功',
							icon:'none',
							duration:2000,
							mask:true,
							success() {
								uni.navigateBack({
									delta:1
								})
								// 清空数据
								that.title = ''
								that.ftitle = ''
								that.price = ''
								that.classify = ''
								that.max = ''
								that.min = ''
								that.unit = ''
							},
							fail() {
								// 清空数据
								that.title = ''
								that.ftitle = ''
								that.price = ''
								that.classify = ''
								that.max = ''
								that.min = ''
								that.unit = ''
							}
						})
					},
					fail: function(err) {
						console.log('商品上传失败')
					}
				});
			}
		},
		// 主标题
		inputValue: function(e) {
			this.title = e.detail.value
		},
		// 副标题
		inputValue1: function(e) {
			this.ftitle = e.detail.value
		},
		// 单位
		inputValue2: function(e) {
			this.unit = e.detail.value
		},
		// 商品分类
		// 价格
		inputValue4: function(e) {
			this.price = e.detail.value
		},
		// 单次最多
		inputValue5: function(e) {
			this.max = e.detail.value
		},
		// 单次最低
		inputValue6: function(e) {
			this.min = e.detail.value
		},
	}
}
